
.modalWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    
}

.backdrop {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.Modal {
    position: relative;
    z-index: 2;
    background: white;
    width: 600px;
    min-height: 400px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    box-shadow: 1px 1px 30px #000;

    &__content {
        width: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        .form-control {
            display: block;
        }
    }

    &__header {
        h1 {
            margin: 0;
            padding: 0;
            font-size: 24px;
            min-height: 50px;
        }
    }

    &__footer {
        display: flex;
        justify-content: flex-end;
    }
}